import { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import microApp, { getActiveApps } from '@micro-zoe/micro-app'
import config from '../config'

const SideBar = () => {
  const history = useHistory()

  // Â≠êÂ∫îÁî®sidebarÈÄöËøápushStateÊéßÂà∂‰∏ªÂ∫îÁî®Ë∑≥ËΩ¨
  function pushState (appName: string, path: string, hash: string) {
    /**
     * ÂΩìÂ≠êÂ∫îÁî®ËøòÊú™Ê∏≤ÊüìÔºåÈÄöËøáÂü∫Â∫ßÊéßÂà∂Ë∑ØÁî±Ë∑≥ËΩ¨ÔºåÂ≠êÂ∫îÁî®Âú®ÂàùÂßãÂåñÊó∂‰ºöËá™Â∑±Ê†πÊçÆurlÊ∏≤ÊüìÂØπÂ∫îÁöÑÈ°µÈù¢
     * ÂΩìÂ≠êÂ∫îÁî®Â∑≤ÁªèÊ∏≤ÊüìÔºåÂàôÁõ¥Êé•ÊéßÂà∂Â≠êÂ∫îÁî®ËøõË°åÂÜÖÈÉ®Ë∑≥ËΩ¨
     *
     * getActiveApps: Áî®‰∫éËé∑ÂèñÊ≠£Âú®ËøêË°åÁöÑÂ≠êÂ∫îÁî®
    */
    if (!getActiveApps().includes(appName)) {
      // child-vite Âíå child-react17Â≠êÂ∫îÁî®‰∏∫hashË∑ØÁî±ÔºåËøôÈáåÊãºÊé•‰∏Ä‰∏ãhashÂÄº
      hash && (path += `/#${hash}`)
      // ‰∏ªÂ∫îÁî®Ë∑≥ËΩ¨
      history.push(path)
    } else {
      let childPath = null
      // child-vite Âíå child-react17Â≠êÂ∫îÁî®ÊòØhashË∑ØÁî±ÔºåhashÂÄºÂ∞±ÊòØÂÆÉÁöÑÈ°µÈù¢Âú∞ÂùÄÔºåËøôÈáåÂçïÁã¨Â§ÑÁêÜ
      if (hash) {
        childPath = hash
      } else {
        // pathÁöÑÂÄºÂΩ¢ÂºèÂ¶ÇÔºö/app-vue2/page2ÔºåËøôÈáå/app-vue2ÊòØÂ≠êÂ∫îÁî®ÁöÑÂü∫Á°ÄË∑ØÁî±Ôºå/page2ÊâçÊòØÈ°µÈù¢Âú∞ÂùÄÔºåÊâÄ‰ª•Êàë‰ª¨ÈúÄË¶ÅÂ∞Ü/app-vue2ÈÉ®ÂàÜÂà†Èô§
        childPath = path.replace(/^\/app-[^/]+/, '')
        !childPath && (childPath = '/') // Èò≤Ê≠¢Âú∞ÂùÄ‰∏∫Á©∫
      }

      // ‰∏ªÂ∫îÁî®ÈÄöËøá‰∏ãÂèëdataÊï∞ÊçÆÊéßÂà∂Â≠êÂ∫îÁî®Ë∑≥ËΩ¨
      microApp.setData(appName, { path: childPath })
    }

  }

  useEffect(() => {
    // üëá ‰∏ªÂ∫îÁî®ÂêësidebarÂ≠êÂ∫îÁî®‰∏ãÂèë‰∏Ä‰∏™Âêç‰∏∫pushStateÁöÑÊñπÊ≥ï
    microApp.setData('appname-sidebar', { pushState })
  })

  return <micro-app name='appname-sidebar' url={`${config.sidebar}/child/sidebar/`}></micro-app>
}

export default SideBar
